.feed-page {
  // background-color: aqua;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  text-align: center;
  .feed-title {
    width: inherit;
  }
  .feed-likes {
    // background-color: bisque;
    width: inherit;
    height: 100%;
    overflow: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  }
  .feed-uplode {
    // background-color: red;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.1);

    width: inherit;
    height: 100%;

    overflow: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
