@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  background-color: transparent !important;
  color: black;
}
.collection-item {
  width: 100%;
}
.navbar-light .navbar-nav .nav-link {
  color: black;
}

#toast-container {
  top: 30px !important;
  right: 20px !important;
  left: auto !important;
}
.navbar {
  z-index: 9999;
}

.material-icons:hover {
  cursor: pointer;
}
.card-img-top {
  width: 100%;
  height: 18vw;
  object-fit: cover;
}
.card-body {
  width: 100%;
  height: 13vw;
  flex-grow: 0;
}

.btn:not(:disabled):not(.disabled) {
  cursor: pointer;
  margin-left: 0;
  /* padding: 10px; */
  padding-bottom: 28px;
}

.item{cursor:pointer;display:grid;font-family:'Times New Roman', Times, serif;width:20pc;height:-webkit-max-content;height:-moz-max-content;height:max-content;grid-template-rows:auto;box-shadow:0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);overflow:hidden;margin:2%;border-radius:5px}.item:hover{box-shadow:0 2px 5px 0 rgba(0,0,0,0.4),0 2px 10px 0 rgba(0,0,0,0.4)}.item .item-img{width:inherit;height:274px;grid-row:2}.item .item-title{color:white;font-family:'Times New Roman', Times, serif;height:40px;overflow:hidden;font-size:30px;width:inherit;position:-webkit-sticky;position:sticky;background-color:rgba(0,0,0,0.3);text-align:center;font-weight:450;grid-row:1}.item .item-footer{grid-row:3;display:grid;grid-template-rows:1fr}.item .item-footer .item-supTitle{padding:0 0 0 6px;font-size:20px;position:relative}.item-buttons{display:flex;width:20pc;height:30px;flex-direction:row;justify-content:space-between}.item-buttons .item-button{border:none;background-color:transparent}.item-buttons .item-button:hover{border:0.5px solid black}

.bla {
  display: inline-block;
  background: #00AFF9 url(http://solarservicegroup.com.au/wp-content/uploads/2019/01/unplugged-vecotr.png) center/cover no-repeat;
  height: 100vh;
  width: 100vw;
  margin: 0;
  color: white;
}

h1 {
  margin: .8em 3rem;
  font: 4em Roboto;
}
p {
  display: inline-block;
  margin: .2em 3rem;
  font: 2em Roboto;
}
.comment-title{text-align:center;margin:1%}.comment-continer{align-content:center;display:flex;flex-direction:column;justify-content:center}.comment-continer .comment{height:400px;box-shadow:0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);overflow:hidden;width:50%;align-self:center;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr}.comment-continer .comment-table{grid-column:1;width:100%}.comment-continer .comment-pic{grid-column:2}.comment-continer .comment-form{width:100%;padding:0 0 0 5px}.comment-continer .backgroundImage{overflow:hidden;width:100%;height:100%;background-position:center;background-size:cover;background-repeat:no-repeat;grid-column:2;grid-row:1/3}.comment-continer .backgroundImage:hover{cursor:pointer;transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.test{overflow:scroll}

.profile{display:grid;width:40pc;height:40pc;box-shadow:0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);margin:20px auto;grid-auto-rows:1fr;align-items:center}.profile .profile-button{display:flex;flex-direction:row;justify-content:space-evenly}.profile .profile-name{font-size:25px;width:74%;-webkit-margin-start:82px;margin-inline-start:82px}.profile .myProfile{text-align:center}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:white;color:white;text-transform:uppercase;font-family:"Open Sans Condensed";font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center;color:black;border:1px solid black}.custom-button:hover{background-color:black;color:black;border:1px solid black}.custom-button:focus{background-color:gray}.custom-button.google-signin{background-color:#4285f4;color:white}.custom-button.google-signin:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button:hover{background-color:black;color:white;border:none}

.feed-page{width:100%;height:100vh;display:flex;flex-direction:row;text-align:center}.feed-page .feed-title{width:inherit}.feed-page .feed-likes{width:inherit;height:100%;overflow:scroll;display:flex;flex-direction:column;align-items:center;box-shadow:0 2px 5px 0 rgba(0,0,0,0.1),0 2px 10px 0 rgba(0,0,0,0.1)}.feed-page .feed-uplode{box-shadow:0 2px 5px 0 rgba(0,0,0,0.1),0 2px 10px 0 rgba(0,0,0,0.1);width:inherit;height:100%;overflow:scroll;display:flex;flex-direction:column;align-items:center}

